	module.exports = {"data":{"markdownRemark":{"html":"<p>上课，继续讲尼采。尼采的学说实际上很值得研究，因为他是在一个世界秩序需要变化的时代提出他的政治思想的，他的目的是建立一种新的秩序，最后寄托于\"超人\"。大凡在历史发生这样的需要的时候，即需要建立新的秩序的时候，总会有这样的思想家，主张用强力和通过强人来确立新的秩序。问题是新的秩序是否是能够建立起来，还是它有自己发展的逻辑，不论人怎样努力，最后还是这样的秩序。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0076.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0076.md","frontmatter":{"title":"4月14日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0076.md absPath of file >>> MarkdownRemark"}}

